.login-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #4a1a4a 0%, #2d1b69 50%, #1a1a2e 100%);
  padding: 20px;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
}

.logo-section-outside {
  text-align: center;
  margin-bottom: 32px;
}

.logo-outside {
  width: 180px;
  height: auto;
  max-height: 180px;
  object-fit: contain;
  display: block;
  margin: 0 auto;
  filter: drop-shadow(0 4px 12px rgba(0, 0, 0, 0.3));
}

.login-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 24px;
  padding: 48px;
  width: 100%;
  max-width: 480px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  flex-direction: column;
}

.logo-section {
  text-align: center;
  margin-bottom: 70px;
  padding-bottom: 40px;
  border-bottom: 1px solid rgba(74, 26, 74, 0.1);
  order: 1;
}

.logo {
  width: 200px;
  height: auto;
  max-height: 200px;
  margin: 0 auto 30px auto;
  border-radius: 50px;
  object-fit: contain;
  display: block;
}

.app-title {
  font-size: 32px;
  font-weight: 700;
  color: #4a1a4a;
  margin: 0;
  letter-spacing: -0.5px;
  line-height: 1.2;
}

.form-section {
  width: 100%;
  order: 2;
  margin-top: 30px;
}

.form-title {
  font-size: 28px;
  font-weight: 600;
  color: #2d1b69;
  margin: 0 0 12px 0;
  text-align: center;
  line-height: 1.3;
}

.form-subtitle {
  font-size: 16px;
  color: #666;
  text-align: center;
  margin-bottom: 32px;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.input-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-input {
  width: 100%;
  padding: 16px 20px;
  border: 2px solid #e1e5e9;
  border-radius: 12px;
  font-size: 16px;
  background: #fff;
  transition: all 0.3s ease;
  box-sizing: border-box;
}

.form-input:focus {
  outline: none;
  border-color: #4a1a4a;
  box-shadow: 0 0 0 3px rgba(74, 26, 74, 0.1);
}

.form-input.error {
  border-color: #e74c3c;
  box-shadow: 0 0 0 3px rgba(231, 76, 60, 0.1);
}

.error-message {
  color: #e74c3c;
  font-size: 14px;
  margin-top: 4px;
}

.submit-btn {
  width: 100%;
  padding: 16px;
  background: linear-gradient(135deg, #4a1a4a 0%, #2d1b69 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 56px;
}

.submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(74, 26, 74, 0.3);
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.loading-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.toggle-form {
  text-align: center;
  margin-top: 24px;
}

.toggle-form p {
  color: #666;
  font-size: 14px;
  margin: 0;
}

.toggle-btn {
  background: none;
  border: none;
  color: #4a1a4a;
  font-weight: 600;
  cursor: pointer;
  text-decoration: underline;
  font-size: 14px;
}

.toggle-btn:hover {
  color: #2d1b69;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .login-container {
    padding: 16px;
  }
  
  .login-card {
    padding: 32px 24px;
    border-radius: 20px;
  }
  
  .logo {
    width: 140px;
    height: auto;
    max-height: 140px;
    margin: 0 auto 24px auto;
    border-radius: 35px;
  }
  
  .app-title {
    font-size: 28px;
    margin-bottom: 12px;
  }
  
  .form-title {
    font-size: 24px;
    margin-bottom: 16px;
  }
  
  .logo-section {
    margin-bottom: 50px;
    padding-bottom: 25px;
  }
  
  .form-section {
    margin-top: 20px;
  }
  
  .form-input {
    padding: 14px 16px;
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .login-card {
    padding: 24px 20px;
  }
  
  .app-title {
    font-size: 24px;
  }
  
  .form-title {
    font-size: 20px;
  }
}

/* Biometric Authentication Styles */
.biometric-option {
  margin: 1.5rem 0;
  padding: 1rem;
  background: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.2);
  border-radius: 12px;
}

.biometric-label {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  cursor: pointer;
}

.biometric-checkbox {
  width: 20px;
  height: 20px;
  cursor: pointer;
  accent-color: #8b5cf6;
}

.biometric-text {
  color: white;
  font-size: 0.95rem;
  font-weight: 500;
}

.biometric-hint {
  margin: 0.5rem 0 0 0;
  padding-left: 28px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.85rem;
  line-height: 1.4;
}

.biometric-button {
  width: 100%;
  padding: 1rem;
  background: linear-gradient(135deg, #8b5cf6 0%, #ec4899 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  box-shadow: 0 4px 12px rgba(139, 92, 246, 0.3);
}

.biometric-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(139, 92, 246, 0.5);
}

.biometric-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.fingerprint-icon {
  font-size: 1.5rem;
}

.divider {
  display: flex;
  align-items: center;
  text-align: center;
  margin: 1.5rem 0;
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.85rem;
  font-weight: 500;
}

.divider::before,
.divider::after {
  content: '';
  flex: 1;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.divider:not(:empty)::before {
  margin-right: 1rem;
}

.divider:not(:empty)::after {
  margin-left: 1rem;
}

.general-error {
  padding: 0.8rem;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  border-radius: 8px;
  margin: 1rem 0;
  text-align: center;
}
